@mixin button-primary($foreground-color, $background-color, $hover-color) {
    background-color: $background-color;
    border: rem(2) solid $background-color;
    box-shadow: 0 0 0 rem(2) $background-color;
    color: $foreground-color;
    margin: rem(2);
    outline: 0;

    &:visited {
        background-color: $background-color;
        border: rem(2) solid $background-color;
        box-shadow: 0 0 0 rem(2) $background-color;
        color: $foreground-color;
    }

    &.is-hover,
    &:hover {
        background-color: $hover-color;
        border: rem(2) solid $hover-color;
        box-shadow: 0 0 0 rem(2) $hover-color;
        color: $foreground-color;
    }

    &.is-focus,
    &:focus {
        background-color: $hover-color;
        border: rem(2) dotted $color-white;
        box-shadow: 0 0 0 rem(2) $hover-color;
        color: $foreground-color;
    }

    &.is-active,
    &:active {
        background-color: $hover-color;
        border: rem(2) solid $color-white;
        box-shadow: 0 0 0 rem(2) $hover-color;
    }

    &.is-disabled,
    &:disabled {
        background-color: $color-cloud;
        border: rem(2) solid $color-cloud;
        box-shadow: 0 0 0 rem(2) $color-cloud;
        color: $color-grey;
        cursor: default;
        pointer-events: none;

        &:visited,
        &:hover,
        &:focus,
        &:active {
            outline: 0;
        }
    }

    &.button--icon-right {
        padding-right: rem(68);
    }

    &.button--icon-left {
        padding-left: rem(68);
    }
}

@mixin button-secondary($foreground-color, $text-color, $hover-color) {
    background-color: transparent;
    border: rem(2) solid transparent;
    box-shadow: 0 0 0 rem(2) $foreground-color;
    color: $foreground-color;
    margin: rem(2);
    outline: 0;

    &:visited {
        background-color: transparent;
        border: rem(2) solid transparent;
        box-shadow: 0 0 0 rem(2) $foreground-color;
        color: $foreground-color;
    }

    &.is-hover,
    &:hover {
        border: rem(2) solid transparent;
        box-shadow: 0 0 0 rem(2) $hover-color;
        color: $hover-color;
    }

    &.is-focus,
    &:focus {
        border: rem(2) dotted $hover-color;
        box-shadow: 0 0 0 rem(2) $text-color, 0 0 0 rem(4) $hover-color;
        color: $hover-color;
        margin: rem(4) rem(2);
        padding: rem(8) rem(26) rem(11);

        &.button--icon-right {
            padding-left: rem(26);
            padding-right: rem(66);

            .icon {
                right: rem(24);
                top: rem(11);
            }
        }

        &.button--icon-left {
            padding-left: rem(66);
            padding-right: rem(26);

            .icon {
                left: rem(24);
                top: rem(11);
            }
        }
    }

    &.is-active,
    &:active {
        border: rem(2) solid $hover-color;
        box-shadow: 0 0 0 rem(2) $text-color, 0 0 0 rem(4) $hover-color;
        color: $hover-color;
        margin: rem(4) rem(2);
        padding: rem(8) rem(26) rem(11);

        &.button--icon-right {
            padding-left: rem(26);
            padding-right: rem(66);

            .icon {
                right: rem(24);
                top: rem(11);
            }
        }

        &.button--icon-left {
            padding-left: rem(66);
            padding-right: rem(26);

            .icon {
                left: rem(24);
                top: rem(11);
            }
        }
    }

    &.is-disabled,
    &:disabled {
        background-color: $color-cloud;
        border: rem(2) solid $color-cloud;
        box-shadow: 0 0 0 rem(2) $color-grey;
        color: $color-grey;
        cursor: default;
        padding: rem(10) rem(26);
        pointer-events: none;

        &:visited,
        &:hover,
        &:focus,
        &:active {
            margin: rem(2);
            outline: 0;
        }
    }

    &.button--icon-left {
        padding-left: rem(68);
    }

    &.button--icon-right {
        padding-right: rem(68);
    }
}

@mixin button-tertiary($foreground-color, $border-color, $hover-color) {
    border: 0;
    color: $foreground-color;
    outline: 0;

    &:visited {
        border: 0;
        color: $foreground-color;
    }

    .button--border {
        border-bottom: rem(1) solid $border-color;
        display: inline-block;
        padding-bottom: rem(1);
    }

    &.is-hover,
    &:hover {
        border: 0;
        color: $hover-color;

        .button--border {
            border-bottom: rem(1) solid $hover-color;
        }
    }

    &.is-focus,
    &:focus {
        border: 0;
        color: $hover-color;

        .button--border {
            border-bottom: rem(2) dotted $hover-color;
            padding-bottom: 0;
        }
    }

    &.is-active,
    &:active {
        border: 0;
        color: $hover-color;

        .button--border {
            border-bottom: rem(2) solid $hover-color;
            padding-bottom: 0;
        }
    }

    &.is-disabled,
    &:disabled {
        color: $color-grey;
        cursor: default;
        pointer-events: none;

        &:focus,
        &:visited,
        &:hover,
        &:active {
            outline: 0;
        }

        .button--border {
            border-bottom: rem(1) solid $color-grey;
        }

        .icon {
            color: $color-grey;
        }
    }
}
