.button {
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
  border: solid 0.125rem transparent;
  border-radius: 3.125rem;
  cursor: pointer;
  display: inline-block;
  font-size: 1.1875rem;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  margin: 0.125rem;
  padding: 0.625rem 1.75rem 0.75rem;
  position: relative;
  text-decoration: none;
}

.button.button--icon-left .icon {
  left: 1.625rem;
}

.button.button--icon-right .icon {
  right: 1.625rem;
}

.button .icon {
  height: 1.5rem;
  line-height: 1;
  position: absolute;
  top: 0.75rem;
  width: 1.5rem;
}

.button--primary {
  background-color: #70a400;
  border: 0.125rem solid #70a400;
  box-shadow: 0 0 0 0.125rem #70a400;
  color: #fff;
  margin: 0.125rem;
  outline: 0;
}

.button--primary:visited {
  background-color: #70a400;
  border: 0.125rem solid #70a400;
  box-shadow: 0 0 0 0.125rem #70a400;
  color: #fff;
}

.button--primary.is-hover, .button--primary:hover {
  background-color: #598300;
  border: 0.125rem solid #598300;
  box-shadow: 0 0 0 0.125rem #598300;
  color: #fff;
}

.button--primary.is-focus, .button--primary:focus {
  background-color: #598300;
  border: 0.125rem dotted #fff;
  box-shadow: 0 0 0 0.125rem #598300;
  color: #fff;
}

.button--primary.is-active, .button--primary:active {
  background-color: #598300;
  border: 0.125rem solid #fff;
  box-shadow: 0 0 0 0.125rem #598300;
}

.button--primary.is-disabled, .button--primary:disabled {
  background-color: #e5e5e5;
  border: 0.125rem solid #e5e5e5;
  box-shadow: 0 0 0 0.125rem #e5e5e5;
  color: #b2b2b2;
  cursor: default;
  pointer-events: none;
}

.button--primary.is-disabled:visited, .button--primary.is-disabled:hover, .button--primary.is-disabled:focus, .button--primary.is-disabled:active, .button--primary:disabled:visited, .button--primary:disabled:hover, .button--primary:disabled:focus, .button--primary:disabled:active {
  outline: 0;
}

.button--primary.button--icon-right {
  padding-right: 4.25rem;
}

.button--primary.button--icon-left {
  padding-left: 4.25rem;
}
